.bl_drawer_checkbox {
  position: fixed;
  left: -50vw;
}

.bl_drawer_btn {
  position: fixed;
  right: 10px;
  bottom: 168px;
  z-index: 100;
  width: 60px;
  height: 60px;
  padding-top: 8px;
  border: 1px solid #c4c4c4;
  background-color: #fff;
  cursor: pointer;
}

.bl_drawer_btnTxt {
  font-size: 0.75rem;
  text-align: center;
}

.bl_drawer_line {
  display: inline-block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  box-sizing: border-box;
  background-color: #333;
  transition: all .5s;
}

.bl_drawer_lineWrapper {
  position: relative;
  width: 30px;
  height: 26px;
  margin: 0 auto 3px auto;
  cursor: pointer;
}

.bl_drawer_line:nth-of-type(1) {
  top: 0;
}

.bl_drawer_line:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
}

.bl_drawer_line:nth-of-type(3) {
  bottom: 0;
}

.bl_drawer_checkbox:checked ~ .bl_drawer_btn .bl_drawer_line:nth-of-type(1) {
  transform: translateY(12px) rotate(45deg);
}

.bl_drawer_checkbox:checked ~ .bl_drawer_btn .bl_drawer_line:nth-of-type(2) {
  opacity: 0;
}

.bl_drawer_checkbox:checked ~ .bl_drawer_btn .bl_drawer_line:nth-of-type(3) {
  transform: translateY(-12px) rotate(-45deg);
}

.bl_drawer_btnTxt {
  transition: 0.3s;
}

.bl_drawer_btnTxtWrapper {
  position: relative;
  height: 18px;
  overflow: hidden;
}

.bl_drawer_btnTxt:nth-of-type(2) {
  visibility: hidden;
}

.bl_drawer_checkbox:checked ~ .bl_drawer_btn .bl_drawer_btnTxt:nth-of-type(1) {
  visibility: hidden;
  transform: translateY(-100%);
}

.bl_drawer_checkbox:checked ~ .bl_drawer_btn .bl_drawer_btnTxt:nth-of-type(2) {
  visibility: visible;
  transform: translateY(-100%);
}

.bl_drawer_background {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  transition: 0.3s;
}

.bl_drawer_checkbox:checked ~ .bl_drawer_background {
  visibility: visible;
  opacity: 1;
}

.bl_drawer_close {
  visibility: hidden;
  position: fixed;
  bottom: 88px;
  left: 0;
  z-index: 110;
  transform: translateX(-100%);
  padding: 5px 10px;
  border: 2px solid #333;
  background-color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.bl_drawer_close:active {
  background-color: #c4c4c4;
}

.bl_drawer_checkbox:checked ~ .bl_drawer_close {
  visibility: visible;
  left: 50%;
  transform: translateX(-50%);
}

.ly_navigation_inner {
  display: flex;
  visibility: hidden;
  justify-content: center;
  align-items: flex-end;
  position: fixed;
  bottom: 68px;
  left: calc(-100% + 80px);
  z-index: 100;
  width: calc(100% - 80px);
  height: 100%;
  padding-bottom: 78px;
  background-color: #fff;
  transition: 0.3s;
}

.bl_drawer_checkbox:checked ~ .ly_navigation_inner {
  visibility: visible;
  transform: translateX(100%);
}

