  }
  .ly_navigation_inner {
    display: block;
    /* ハンバーガーメニューの打ち消し */
    visibility: visible;
    /* ハンバーガーメニューの打ち消し */
    position: static;
    /* ハンバーガーメニューの打ち消し */
    width: 100%;
    /* ハンバーガーメニューの打ち消し */
    max-width: 1220px;
    height: 100%;
    margin: 0 auto;
    padding: 0 10px;
  }
}

@media screen and (min-width: 768px) {
  .bl_headerNav {
    display: flex;
    flex-direction: row;
    /* ハンバーガーメニューの打ち消し */
    height: 100%;
  }
  .bl_headerNav_item {
    display: flex;
    flex-grow: 1;
    position: relative;
    border: none;
    /* ハンバーガーメニューの打ち消し */
  }
  .bl_headerNav_item:last-child {
    border: none;
    /* ハンバーガーメニューの打ち消し */
  }
  .bl_headerNav_item::before {
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
    width: 1px;
    height: 35px;
    background-color: #b8b9dc;
    content: "";
  }
  .bl_headerNav_item:last-child::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
    width: 1px;
    height: 35px;
    background-color: #b8b9dc;
    content: "";
  }
  .bl_headerNav_link {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    padding: 0 10px;
    color: inherit;
    text-decoration: none;
  }
  .bl_headerNav_link:hover {
    color: #ff993c;
  }
  .bl_headerNav_link:hover .bl_headerNav_txt::after {
    left: 0;
    width: 100%;
  }
  .bl_headerNav_txt {
    display: flex;
    align-items: center;
    position: relative;
    font-size: 1rem;
    /* ハンバーガーメニューの打ち消し */
    font-weight: bold;
    line-height: 1;
    white-space: nowrap;
  }
  .bl_headerNav_txt::after {
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 4px;
    border-radius: 5px;
    background-color: #ff993c;
