.bl_accordion {
  position: relative;
  width: 100%;
  border-top: none;
  border-bottom: 1px solid #c4c4c4;
  outline: 0;
  cursor: pointer;
}

.bl_accordion_title {
  position: relative;
}

.bl_accordion_title::after {
  display: block;
  position: absolute;
  top: 42%;
  right: 25px;
  transform: rotate(135deg);
  width: 8px;
  height: 8px;
  border-top: solid 2px #333;
  border-right: solid 2px #333;
  content: "";
  transition: all .2s ease-in-out;
}

.bl_accordion_title.open:after {
  top: 45%;
  transform: rotate(-45deg);
}

.bl_accordion_content {
  display: none;
  margin-bottom: 20px;
  margin-left: 40px;
  padding: 20px;
  background-color: #f8f8f8;
}

.bl_accordion .bl_accordion_title {
  display: block;
  position: relative;
  margin: 0;
  padding: 20px 50px 20px 20px;
  border-top: 1px solid #c4c4c4;
  font-size: 1.25em;
  font-weight: normal;
  cursor: pointer;
  transition: 0.3s;
}

.bl_accordion .bl_accordion_title:active {
  background-color: #ff993c;
  color: #fff;
}

.bl_accordion .bl_accordion_title:active::after {
  border-color: #fff;
}

@media screen and (min-width: 768px) {
  .bl_accordion .bl_accordion_title:hover, .bl_accordion .bl_accordion_title:focus {
    background-color: #ff993c;
    color: #fff;
  }
  .bl_accordion .bl_accordion_title:hover::after, .bl_accordion .bl_accordion_title:focus::after {
    border-color: #fff;
  }
}

.bl_toTop {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  right: 10px;
  bottom: 74px;
  z-index: 10000;
  transform: translateY(20px);
  transition: 0.3s;
}

.bl_toTop_chevron {
  display: block;
  position: relative;
  width: 48px;
  height: 48px;
  border: 4px solid #ff993c;
  background-color: #ff993c;
  transition: 0.3s;
}

.bl_toTop_chevron::before {
  position: absolute;
  top: 8px;
  right: 0;
  bottom: 0;
  left: 0;
  transform: rotate(135deg);
  width: 12px;
  height: 12px;
  margin: auto;
  border: 4px solid #fff;
  border-top: none;
  border-right: none;
  content: "";
  transition: 0.3s;
}

.bl_toTop_chevron:active {
  background-color: #fff;
}

.bl_toTop_chevron:active::before {
  border-color: #ff993c;
}

.bl_toTop.js_visible {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

@media screen and (min-width: 768px) {
  .bl_toTop {
    right: 20px;
    bottom: 20px;
  }
  .bl_toTop_chevron {
    width: 64px;
    height: 64px;
  }
  .bl_toTop_chevron::before {
    top: 12px;
    width: 20px;
    height: 20px;
  }
  .bl_toTop_chevron:hover {
    background-color: #fff;
  }
  .bl_toTop_chevron:hover::before {
    border-color: #ff993c;
  }
}

