.el_picture10 {
  position: relative;
  padding-top: 10%;
  overflow: hidden;
  > img {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 100%;

    object-fit: cover;
    object-position: center center;
  }
}



